import React from "react"
import Select from "./Select"
import "../assets/css/Tune.css"


const Tune = () => {
	return (
		<div className="jumbotron vertical-center">
			<div className="container">
				<h1>Tune</h1>
				<br/>
				<br/>
				<form>
					<Select/>
					<br/>
					<br/>
					<button className="btnTune" type="submit">Tune</button>
				</form>
			</div>
		</div>
	)
}

export default Tune
